import createOrthoCamera from './orthoCamera';
import resizeCanvas from './resizeCanvas';
const defaultOptions = {
  autoUpdate: true,
  pixelRatio: undefined
};
export default ((canvas, width, height, options) => {
  options = { ...defaultOptions,
    ...options
  };
  const gl = canvas.getContext('webgl');
  let {
    autoUpdate,
    pixelRatio
  } = options;
  pixelRatio = pixelRatio || window.devicePixelRatio || 1;
  const viewportInfo = {
    pixelRatio,
    x: 0,
    y: 0,
    width,
    height
  };
  let resizeHandler;
  const [vWidth, vHeight] = resizeCanvas(canvas, pixelRatio);
  const camera = createOrthoCamera(width, height, vWidth, vHeight);

  if (autoUpdate) {
    resizeHandler = e => {
      viewportObject.update();
    };

    window.addEventListener('resize', resizeHandler);
  }

  const viewportObject = {
    getViewportInfo() {
      return viewportInfo;
    },

    getCamera() {
      return camera;
    },

    getContext() {
      return gl;
    },

    update() {
      let [vWidth, vHeight] = resizeCanvas(canvas, pixelRatio);
      const ratio = width / height;
      const vRatio = vWidth / vHeight;
      let displayWidth, displayHeight;

      if (ratio < vRatio) {
        displayHeight = vHeight;
        displayWidth = vHeight * ratio;
      } else {
        displayWidth = vWidth;
        displayHeight = vWidth / ratio;
      }

      const displayX = (vWidth - displayWidth) / 2;
      const displayY = (vHeight - displayHeight) / 2;
      viewportInfo.x = displayX;
      viewportInfo.y = displayY;
      viewportInfo.width = displayWidth;
      viewportInfo.height = displayHeight;
      gl.viewport(displayX, displayY, displayWidth, displayHeight);
      camera.resize(width, height, displayWidth, displayHeight);
    },

    cleanUp() {
      if (resizeHandler) {
        window.removeEventListener('resize', resizeHandler);
      }
    }

  };
  autoUpdate && viewportObject.update();
  return viewportObject;
});